.home-page {
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  padding: 80px 1.5rem 100px;
  display: flex;
}

.section-title {
  color: var(--text-color);
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
}

.streak-tracker {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

.daily-task-card {
  background: var(--card-background);
  text-align: center;
  box-shadow: 0 10px 30px var(--shadow-light);
  border-radius: 20px;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  overflow: hidden;
}

.calendar-container {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

.calendar-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

.date-selectors {
  gap: .5rem;
  display: flex;
}

.date-selectors select {
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: .3rem .5rem;
  font-size: .9rem;
}

.calendar-weekdays {
  text-align: center;
  color: var(--light-text-color);
  grid-template-columns: repeat(7, 1fr);
  margin-bottom: .5rem;
  font-size: .8rem;
  display: grid;
}

#share-calendar-btn {
  border: 1px solid var(--primary-color);
  width: 100%;
  color: var(--primary-color);
  cursor: pointer;
  background: none;
  border-radius: 8px;
  margin-top: 1rem;
  padding: .5rem;
  font-size: .9rem;
}
