/* --- ANA SAYFA STİLLERİ --- */
.home-page {
    padding: 80px 1.5rem 100px 1.5rem; /* Header ve Nav bar için boşluk */
    display: flex;
    flex-direction: column;
    align-items: center; /* Tüm içeriği ortala */
    gap: 1.5rem;
}

.section-title {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--text-color);
    text-align: center;
}

/* Haftalık Seri Takibi */
.streak-tracker {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
}

/* ... (streak styles remain the same) ... */

/* Günün İyilik Görevi Kartı */
.daily-task-card {
    background: var(--card-background);
    border-radius: 20px;
    padding: 2rem;
    text-align: center;
    box-shadow: 0 10px 30px var(--shadow-light);
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
}

/* ... (task card styles remain the same) ... */

/* Takvim Stilleri */
.calendar-container {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
}

.calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.date-selectors {
    display: flex;
    gap: 0.5rem;
}

.date-selectors select {
    border: 1px solid var(--border-color);
    border-radius: 8px;
    padding: 0.3rem 0.5rem;
    font-size: 0.9rem;
}

.calendar-weekdays {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    text-align: center;
    margin-bottom: 0.5rem;
    font-size: 0.8rem;
    color: var(--light-text-color);
}

#share-calendar-btn {
    width: 100%;
    margin-top: 1rem;
    background: none;
    border: 1px solid var(--primary-color);
    color: var(--primary-color);
    padding: 0.5rem;
    border-radius: 8px;
    cursor: pointer;
    font-size: 0.9rem;
}

/* ... (rest of the styles remain the same) ... */
